import * as ApiPath from "api/ApiPath";
import WebApi from "api/WebApi";
export function Login(username, password) {
    return WebApi.Post(ApiPath.login, {
        username,
        password
    }).then((resp) => {
        this.setUser(resp.result);
        return resp;
    });
}
export function Register(username, email, password) {
    return WebApi.Post(ApiPath.register, {
        username,
        email,
        password
    }).then((resp) => {
        this.setUser(resp.result);
        return resp;
    });
}
export function Logout() {
    return WebApi.Post(ApiPath.logout, {}).then((resp) => {
        this.setUser({});
        return resp;
    });
}
export function WhoAmI() {
    return WebApi.Post(ApiPath.checkMe, {}).then((resp) => {
        this.setUser(resp.result);
        return resp;
    });
}
export default {
    Login,
    Register,
    Logout
};



